body{
    margin : 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #eff3f7;
    height: 100%;
}

html,
body,
.container
{
    height: 100%;
    display: flex;
    justify-content: center;
}

.Loader
{
    width:100%; 
    height:100%;
    display: flex;
    justify-content: center;
    align-items: center ;
    font-weight: 300;
}

.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 50px;
    width : 80%;
}

.movie
{
    width:  500px;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color:#adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 
                0 8px 16px -8px rgba(0, 0, 0, 0.3),
                0 -6px 16px -6px rgba(0, 0, 0, 0.25);
}

.movie img 
{
    position: relative;
    top : -50px;
    width: 100%;
    max-width : 120px;
    margin-right:  30px;
}

.movie .movie_title,
.movie .movie_year
{
    margin:  0;       
    font-weight: 300;
}

.movie .movie_title
{
    margin-bottom: 5px;
    margin-right:  80px;
    font-size: 24px;
    color:#2c2c2c;
}

.movie .movie_genres
{
    list-style: none;
    padding: 0;
    margin: 5px;    
    display: flex;
    margin-right: 0px;
    font-size: 24px;
}
